import React from 'react';
import { useTheme, ThemeType } from './ThemeContext';  // nh·ªõ import ThemeType
import './ThemeToggle.css';

const themes: ThemeType[] = [
  'pinkOrange',
  'purplePink',
  'yellowPurple',
  'darkCherry',
  'neonMatrix',
  'sunsetPeach',
  'cyberGold',
  'forestNight',
  'blushRose',
  'aquaDream',
  'midnightInk',
];  // khai b√°o ki·ªÉu ThemeType[]

const ThemeToggle: React.FC = () => {
  const { theme, setTheme } = useTheme();

  const nextTheme = () => {
    const currentIndex = themes.indexOf(theme);
    const nextIndex = (currentIndex + 1) % themes.length;
    setTheme(themes[nextIndex]);  // gi·ªù ƒë√¢y h·ª£p l·ªá v√¨ themes[nextIndex] l√† ThemeType
  };

  const getEmoji = () => {
  switch (theme) {
    case 'pinkOrange': return 'üåÖ';
    case 'purplePink': return 'üéÜ';
    case 'yellowPurple': return 'üå†';
    case 'darkCherry': return 'üçí';
    case 'neonMatrix': return 'üß™';
    case 'sunsetPeach': return 'üåá';
    case 'cyberGold': return 'üåü';
    case 'forestNight': return 'üå≤';
    case 'blushRose': return 'üå∏';
    case 'aquaDream': return 'üåä';
    case 'midnightInk': return 'üñãÔ∏è';
    default: return 'üé®';
  }
};


  return (
    <button
      className="theme-toggle-btn"
      onClick={nextTheme}
      aria-label="Toggle theme"
      title="Click ƒë·ªÉ ƒë·ªïi theme"
    >
      {getEmoji()}
    </button>
  );
};

export default ThemeToggle;